* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
header {
  padding: 1rem 0;
  background-color: #333;
  color: white;
  text-align: center;
  font-size: 2rem;
}
.con > * {
}
.con > li * {
}
.list {
  max-width: 1200px;
  width: calc(100% -2rem);
  margin: auto;
}
.list > li {
  padding: 1rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-bottom: 1px dotted black;
}
.list > li > p:nth-child(odd) {
  width: 70%;
}
.list > li > p:nth-child(even) {
  width: 30%;
}
.list > li > p:nth-child(2) {
  text-align: end;
}

.btn_good {
  cursor: pointer;
}

/* modal */
.modal_con {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal {
  background-color: white;
  border-radius: 10px;
  padding: 1rem;
  min-width: 300px;
  max-width: 500px;
  width: 50%;
  position: relative;
}
.modal h1 {
  text-align: center;
}
.modal p {
  padding: 1rem 0;
}
.modal .close {
  position: absolute;
  top: 0;
  right: -50px;
}
.inputCon {
  max-width: 800px;
  width: calc(100% -2rem);
  min-width: 500px;
  margin: auto;
  display: flex;
}